import React, { useState } from "react";
import Particles from 'react-particles-js';
import PersonIcon from '@material-ui/icons/Person';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import CallIcon from '@material-ui/icons/Call';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import RoomIcon from '@material-ui/icons/Room';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';

const Contact = () => {

    const [data, setData] = useState(
        {
            name : "",
            email : "",
            subject : "",
            message : ""
        }
    );

    const InputEvent = (event) => {
        const {name,value} = event.target;

        setData((preVal) =>{
            return{
                   ...preVal,
                   [name] : value
            };
        })
    }

    const formSubmit = (e) => {
        e.preventDefault();
        alert(`Thank you for your message. We will response you asap ${data.name}`)

    }


    return(
        <>
        <Particles 
           className=""
           params={{particles: { "size": { "value": 3},line_linked: { enable:false,}}}}
        />
        
        <div className="container" id="contactform">
            <div className="row">
                <div className="col my-5">
                   <h1 className="page-title my-5">Contact Us</h1>
                   <div className="contact-page">
                       <div className="row">
                           <div className="col-md-6">
                           <form onSubmit={formSubmit}>
                            <div className="form-group">
                                <label className="label" htmlFor="InputName">Name</label>
                                <input type="text" className="form-control" name="name" value={data.name} onChange={InputEvent} placeholder="Enter your name" required id="InputName" />
                            </div>
                            <div className="form-group">
                                <label className="label" htmlFor="InputEmail">Email</label>
                                <input type="email" className="form-control" name="email" value={data.email} onChange={InputEvent} placeholder="Enter your email" required id="InputEmail" />
                            </div>
                            <div className="form-group">
                                <label className="label" htmlFor="InputSubject">Subject</label>
                                <input type="text" className="form-control" name="subject" value={data.subject} onChange={InputEvent} placeholder="Enter your subject" required id="InputSubject" />
                            </div>
                            <div className="form-group">
                                <label className="label" htmlFor="InputTextarea">Message</label>
                                <textarea className="form-control" name="message" value={data.message} onChange={InputEvent} placeholder="Message..." required id="InputTextarea" rows="3"></textarea>
                            </div>
                            <button type="submit" className="btn header-btn mb-5">Submit</button>
                            </form>
                           </div>
                           <div className="col-md-6">
                               <h3 className="detail-heading text-md-right">Contact Details</h3>
                               <Detail title ="Shivank Varshney" icon ={<PersonIcon />} />
                               <Detail title ="shivank1703@gmail.com" icon ={<MailOutlineIcon />}/>
                               <Detail title ="9058878893" icon ={<CallIcon />} />
                               <Detail title ="9058878893" icon ={<WhatsAppIcon />} />
                               <Detail title ="Bahjoi, Sambhal(Uttar Pradesh), INDIA" icon ={<RoomIcon />} />
                               <h3 className="detail-heading text-md-right mt-3">Links</h3>
                               <Link title ="GITHUB" icon ={<GitHubIcon />} link ="https://github.com/Shivank-Varshney" />
                               <Link title ="LINKEDIN" icon ={<LinkedInIcon />} link ="https://www.linkedin.com/in/shivank-varshney-4745aa192" />
                               <Link title ="FACEBOOK" icon ={<FacebookIcon />} link ="https://www.facebook.com/shivank1703" />
                               <Link title ="INSTAGRAM" icon ={<InstagramIcon />} link="https://www.instagram.com/shiv_ank17" />
                           </div>
                       </div>
                   </div>
                </div>
            </div>
        </div>
        </>
    );
}

const Detail = (props) =>{
    return(
        <h5 className="contact-detail text-md-right"> {props.title}  
            <span className="contact-icon"> {props.icon}</span>
        </h5>
    );
}

const Link = (props) =>{
    return(
        <a className="contact-detail-link d-flex justify-content-md-end my-2" href={props.link}>{props.title}&nbsp;&nbsp; 
            <span className="contact-icon"> {props.icon}</span>
        </a>
    );
}

export default Contact;