import React, {useState} from 'react';
import {Drawer, List, Divider, ListItem, ListItemText} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import { NavLink } from 'react-router-dom'; 

const styles = { 
sideNav: { 
	zIndex: 3, 
	marginLeft: '0px', 
	position: 'absolute', 
    left:"20px",
    top:"10px",
}, 
link: { 
	color: 'Black', 
	textDecoration: 'none',
} 
}; 

 const Toggler = () => {
  const [state, setState] = useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };


  return (
    <div>
      {[""].map((anchor) => (
        <React.Fragment key={anchor}>
          <MenuIcon onClick={toggleDrawer(anchor, true)}></MenuIcon>
          <Drawer open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            <div onClick={toggleDrawer(anchor, false)}>
				<h1 className="menu-title" >SHIVANK</h1>
				<p className="menu-subtitle">FrontEnd Developer</p>
				<Menu activeClassName="activecls" menuNameAddress = "/" menuName = "home" />
				<Menu activeClassName="activecls" menuNameAddress = "/about" menuName = "about" />
				<Menu activeClassName="activecls" menuNameAddress = "/work" menuName = "work" />
				<Menu activeClassName="activecls" menuNameAddress = "/skill" menuName = "skill" />
				<Menu activeClassName="activecls" menuNameAddress = "/contact" menuName = "contact" />
			</div>
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}




//////////////////////////////////// *   menu link component start  *//////////////////////////////////////
const Menu = (props) =>{
	return(
		<>
			<NavLink to={props.menuNameAddress} style={styles.link}> 
			<List> 
			<ListItem button key={props.menuName}>
				<ListItemText primary={props.menuName} /> 
			</ListItem> 
			</List> 
		    </NavLink> <Divider />
		</>
	);
}
//////////////////////////////////// *   menu link component End  *//////////////////////////////////////

export default Toggler;