import React from 'react';
import Toggler from "./components/Toggler";
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';

const Navbar = () => {
    return(
        <>
            <div className="container-fluid">
                <div className="row">
                    <div className="col brand">
                        <nav className="navbar navbar-expand-lg navbar-light">
                            <Toggler />
                            <a href="https://www.facebook.com/shivank1703" className="navbar-icon ml-auto">
                               <FacebookIcon />
                            </a>
                            <a href="https://www.instagram.com/shiv_ank17" className="navbar-icon mx-4">
                               <InstagramIcon />
                            </a>
                            <a href="https://www.linkedin.com/in/shivank-varshney-4745aa192" className="navbar-icon">
                               <LinkedInIcon />
                            </a>
                        </nav>
                    </div>
                </div>
            </div>
        </>
    );
} 


export default Navbar;



