import React from "react";
import Particles from 'react-particles-js';
import Tdata from "./components/Tdata";

const Work = () => {
    return(
        <>
        <Particles 
           className=""
           params={{particles: { "size": { "value": 3},line_linked: { enable:false,}}}}
        />

        <div className="container">
            <div className="row">
                <div className="col my-5">
                <h1 className="page-title my-5">Work</h1>
                <div className="work-page">
                <div className="row my-5">
                    {Tdata.map((val,index) => {
                        return(
                            <Template 
                            key = {index}
                            link = {val.href}
                            imgsrc ={val.imgsrc}
                            />
                            );
                    })}
                </div>
                </div>
                </div>
            </div>
        </div>
        </>
    );
}



const Template = (props) => {
    return(
        <div className="col-md-6 my-3">
            <a href={props.link}>
                <img src={props.imgsrc} className="img-fluid work-img" alt="work"/>
            </a>
        </div>
    );
}

export default Work;