import React from "react";
import Particles from 'react-particles-js';
import SkillCard from "./components/SkillCard";
import SkillData from "./components/SkillCardData";

const Footer = () => {
    return(
        <>
        <Particles 
           className=""
           params={{particles: { "size": { "value": 3},line_linked: { enable:false,}}}}
        />

        <div className="container">
            <div className="row">
                <div className="col my-5">
                   <h1 className="page-title my-5">Skills</h1>
                   <div className="skill-page">
                        <div className="row">
                            { SkillData.map((val,index)=>
                                <SkillCard 
                                key = {index}
                                title = {val.title}
                                text = {val.text}
                                />)
                            }
                        </div>
                   </div>
                </div>
            </div>
        </div>
        </>
    );
}

export default Footer;