import React from "react";
import Particles from 'react-particles-js';
import {NavLink} from "react-router-dom";

const Home = () => {
    return(
        <>
        <Particles 
           className=""
           params={{particles: { "size": { "value": 3},line_linked: { enable:false,}}}}
        />

        <div className="container-fluid header">
            <div className="row">
               <div className="container">
                   <div className="row my-5">
                       <div className="col-md-6 my-5">
                           <h1 className="header-title mt-5">Shivank varshney</h1>
                           <p className="header-subtitle my-4">I just want to make beautiful things even if nobody cares.</p>
                           <NavLink to="/about" className="header-btn">Read More</NavLink>
                       </div>
                   </div>
               </div>
            </div>
        </div>
        </>
    );
}

export default Home;